import Vue from 'vue'
import Router from 'vue-router'
import DataFlowPage from '@/views/DataFlowPage/index'
import Test from "@/views/Test/test"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DataFlowPage',
      component: Test
    },
    {
        path:"/test",
        name:"test",
        component:Test
    }
  ]
})



